/* colors */
$blue: #293b49;
$pink: #fdabaf;
$pink-light: #ffe0e6;
$black: #333;
$black-light: rgba(51, 51, 51, 0.7);
$white: white;


.not-found-page {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 100vh;
    transform: translateY(-100px);
}

.error {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-content: center;
}

.number {
    font-weight: 900;
    font-size: 15rem;
    line-height: 1;
}

.illustration {
    position: relative;
    width: 12.2rem;
    margin: 0 2.1rem;
}

.circle {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 12.2rem;
    height: 11.4rem;
    border-radius: 50%;
    background-color: $blue;
}

.clip {
    position: absolute;
    bottom: 0.3rem;
    left: 50%;
    transform: translateX(-50%);
    overflow: hidden;
    width: 12.5rem;
    height: 13rem;
    border-radius: 0 0 50% 50%;
}

.paper {
    position: absolute;
    bottom: -0.3rem;
    left: 50%;
    transform: translateX(-50%);
    width: 9.2rem;
    height: 12.4rem;
    border: 0.3rem solid $blue;
    background-color: $white;
    border-radius: 0.8rem;

    &::before {
        content: "";
        position: absolute;
        top: -0.7rem;
        right: -0.7rem;
        width: 1.4rem;
        height: 1rem;
        background-color: $white;
        border-bottom: 0.3rem solid $blue;
        transform: rotate(45deg);
    }
}

.face {
    position: relative;
    margin-top: 2.3rem;
}

.eyes {
    position: absolute;
    top: 0;
    left: 2.4rem;
    width: 4.6rem;
    height: 0.8rem;
}

.eye {
    position: absolute;
    bottom: 0;
    width: 0.8rem;
    height: 0.8rem;
    border-radius: 50%;
    background-color: $blue;
    animation-name: eye;
    animation-duration: 4s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;

    &-left {
        left: 0;
    }

    &-right {
        right: 0;
    }
}

@keyframes eye {
    0% {
        height: 0.8rem;
    }

    50% {
        height: 0.8rem;
    }

    52% {
        height: 0.1rem;
    }

    54% {
        height: 0.8rem;
    }

    100% {
        height: 0.8rem;
    }
}

.rosyCheeks {
    position: absolute;
    top: 1.6rem;
    width: 1rem;
    height: 0.2rem;
    border-radius: 50%;
    background-color: $pink;

    &-left {
        left: 1.4rem;
    }

    &-right {
        right: 1.4rem;
    }
}

.mouth {
    position: absolute;
    top: 3.1rem;
    left: 50%;
    width: 1.6rem;
    height: 0.2rem;
    border-radius: 0.1rem;
    transform: translateX(-50%);
    background-color: $blue;
}

.text {
    margin-top: 5rem;
    font-weight: 300;
    color: $blue;
}

.button {
    margin-top: 4rem;
    padding: .6rem 3rem;
    color: $white;
    background-color: $black;

    &:hover {
        color: $white;
        background-color: $black-light;
    }
}

.by {
    position: absolute;
    bottom: 0.5rem;
    left: 0.5rem;
    text-transform: uppercase;
    color: $blue;
}
