
@import url("https://fonts.googleapis.com/css?family=Open+Sans:400,600");
.my-cv-page{
    font-family: "Open Sans", Arial, sans-serif;
    max-width: 1200px;
    margin: 0.5in auto;
    box-shadow: 0px 0px 6px #cccccc;
    padding: 0.5in;
    line-height: 1.5em;
    font-size: 14px;
    color: #34495e;
    html,
    body,
    div,
    span,
    applet,
    object,
    iframe,
    h1,
    h2,
    h3,
    h4,
    h5,
    h6,
    p,
    blockquote,
    pre,
    a,
    abbr,
    acronym,
    address,
    big,
    cite,
    code,
    del,
    dfn,
    em,
    img,
    ins,
    kbd,
    q,
    s,
    samp,
    small,
    strike,
    strong,
    sub,
    sup,
    tt,
    var,
    b,
    u,
    i,
    center,
    dl,
    dt,
    dd,
    ol,
    ul,
    li,
    fieldset,
    form,
    label,
    legend,
    table,
    caption,
    tbody,
    tfoot,
    thead,
    tr,
    th,
    td,
    article,
    aside,
    canvas,
    details,
    embed,
    figure,
    figcaption,
    footer,
    header,
    hgroup,
    main,
    menu,
    nav,
    output,
    ruby,
    section,
    summary,
    time,
    mark,
    audio,
    video {
        margin: 0;
        padding: 0;
        border: 0;
        font-size: 100%;
        font: inherit;
        vertical-align: baseline;
    }
    article,
    aside,
    details,
    figcaption,
    figure,
    footer,
    header,
    hgroup,
    main,
    menu,
    nav,
    section {
        display: block;
    }
    *[hidden] {
        display: none;
    }
    ol,
    ul {
        list-style: none;
    }
    blockquote,
    q {
        quotes: none;
    }
    blockquote:before,
    blockquote:after,
    q:before,
    q:after {
        content: "";
        content: none;
    }
    table {
        border-collapse: collapse;
        border-spacing: 0;
    }
    * {
        box-sizing: border-box;
    } 
    a {
        color: #34495e;
        text-decoration: none;
        line-height: 1.5em;
    }
    .container {
        max-width: 1140px;
        margin: auto;
    }
    .heading-style {
        background: #3498db;
        height: 2px;
        width: 100%;
    }
    .section {
        margin-bottom: 40px;
    }
    .main-columns {
        display: flex;
        flex-wrap: wrap;
        margin: 20px 0;
    }
    .main-columns > div:first-child {
        width: 20%;
        text-align: right;
    }
    .main-columns > div:nth-child(2) {
        padding-left: 20px;
        width: 75%;
    }
    .propic {
        float: right;
        // background: url("http://sajib.local/wp-content/uploads/2020/11/propic.jpg") center center;
        background-size: cover !important;
        height: 100%;
        width: 150px;
    }
    ul li {
        margin-bottom: 10px;
        width: 100%;
    }
    ul li:last-child {
        margin: 0;
    }
    ul li i {
        margin-right: 10px;
        color: #3498db;
    }
    .title {
        font-weight: bold;
        text-transform: uppercase;
    }
    .subtitle {
        color: #3498db;
    }
    table {
        width: 100%;
    }
    table td {
        border: 1px solid #dadada;
        padding: 5px 10px;
    }
    table td:first-child {
        border-left: 0;
    }
    table td:last-child {
        border-right: 0;
    }
    .uppercase {
        text-transform: uppercase;
    }
    strong,
    .bold {
        font-weight: bold;
    }
    .italic {
        font-style: italic;
    }
    .text-right {
        text-align: right;
    }
    .text-center {
        text-align: center;
    }
    .flex-v-center {
        display: flex;
        align-items: center;
    }
    .text-small {
        font-size: 11px;
        line-height: 1.5em;
    }
    .text-medium {
        font-size: 13px;
    }
    sup,
    .sup {
        vertical-align: super;
    }
    b{
        font-weight: 700;
    }
    table tr td:first-child {
        width: 160px;
    }
    .satement > p{
        margin-bottom: 10px;
    }
}